<template>
	<div class="finance-products-risk-search">
		<mt-cell title=""></mt-cell>
		<mt-cell title="" v-model="detail.custrisk" ></mt-cell>
		<mt-cell title="" v-model="detail.riskenddate" ></mt-cell>
		<mt-cell class="colorRed" title="" v-model="detail.totalpoint" ></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
			<mt-button @click.native="submit" type="primary"></mt-button>
		</div>
		<div class="field-warn">
			<br>

		</div>
	</div>
</template>

<script>

import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			detail: {
				custrisk: '',
				riskenddate: '',
				totalpoint: ''
			}
		};
	},
	methods: {
        submit() {
        	this.$router.push({name: 'FinanceProductsRiskAssess'});
        },
        back() {
        	history.back();
        },
        getUserRisk(depositacct) {
        	service.getUserRisk({
        		DEPOSITACCT: '6217788370100836435'
        	})
        	.then((data) => {
        		this.detail.custrisk = data.CUSTRISK;
        		this.detail.riskenddate = data.RISKENDDATE;
        		this.detail.totalpoint = data.TOTALPOINT;
        	}, (err) => {
        		console.log(err);
        	});
        }
	},
	created() {
		this.getUserRisk(this.$route.params.depositacct);
	}
};

</script>

<style lang="scss">
	
.finance-products-risk-search{
	.mint-cell-title{
		width: 5rem;
	}
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}

</style>


